export default {
	data() {
		return {
			Router:this.$Router,
			current:1,
			cuponList:[
				{},{},{}
			],
			searchItem:{
				use_step:1
			},
			mainData:[]
		}
	},
	
	
	onLoad() {
		const self = this;
		self.paginate = self.$Utils.cloneForm(self.$AssetsConfig.paginate);
		self.$Utils.loadAll(['getMainData'], self);
	},
	
	onReachBottom() {
		console.log('onReachBottom')
		const self = this;
		if (!self.isLoadAll && uni.getStorageSync('loadAllArray')) {
			self.paginate.currentPage++;
			self.getMainData()
		};
	},

	methods: {
		
		change(current) {
			const self = this;
			if(current!=self.current){
				self.current = current
				if(self.current==1){
					self.searchItem.use_step=1
				}else if(self.current==2){
					self.searchItem.use_step=2
				}else(
					self.searchItem.use_step=-1
				)
				self.getMainData(true)
			}
		},
		
		getMainData(isNew) {
			const self = this;
			if (isNew) {
				self.mainData = [];
				self.paginate = {
					count: 0,
					currentPage: 1,
					is_page: true,
					pagesize: 5
				}
			};
			const postData = {};
			postData.tokenFuncName = 'getProjectToken';
			postData.paginate = self.$Utils.cloneForm(self.paginate);
			postData.searchItem = self.$Utils.cloneForm(self.searchItem);
			const callback = (res) => {
				if (res.info.data.length > 0) {
					self.mainData.push.apply(self.mainData, res.info.data);
					for (var i = 0; i < self.mainData.length; i++) {
						self.mainData[i].createTime = self.mainData[i].create_time.substr(0,10);
						self.mainData[i].invalidTime = self.$Utils.timeto(self.mainData[i].invalid_time,'ymd')
					}
				} else {
					self.$Utils.showToast('没有更多了', 'none');
				};
				console.log('self.mainData', self.mainData)
				self.$Utils.finishFunc('getMainData');
			};
			self.$apis.userCouponGet(postData, callback);
		},
	}
}
